import React from 'react';
import { Col, Form } from 'antd';
// import { SelectAjax } from 'components/common';
// import { getPolicyObjects } from 'services/policyObject';
// import { getEthnicity } from 'services/ethnicity';
// import { getSubsidizeObjects } from 'services/subsidizeObject';
// import { getPrioritizeObjects } from 'services/prioritizeObject';
import moment from 'moment';
import IWL from '../component/InformationWithLabel';

const formatDate = date => {
  if (!date) return '';
  return moment(date).format('YYYY-MM-DD');
};

const InfoBackground = props => {
  const { student } = props;

  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <IWL label="CMND" content={student.idCard} />
      </Col>
      <Col span={12}>
        <IWL label="Ni cp" content={student.placeIssue} />
      </Col>
      <Col span={12}>
        <IWL label="Ngy cp" content={formatDate(student.dateIssue)} />
      </Col>
      <Col span={12}>
        <IWL label="Dn tc" content={student.ethnicity} />
      </Col>
      <Col span={12}>
        <IWL label="Tn gio" content={student.religion} />
      </Col>
      <Col span={12}>
        <IWL label="on" content={formatDate(student.unionDate)} />
      </Col>
      <Col span={12}>
        <IWL label="ng" content={formatDate(student.politicalDate)} />
      </Col>
      <Col span={12}>
        <IWL label="TCS" content={student.policyObject} />
      </Col>
      <Col span={12}>
        <IWL label="T TC" content={student.subsidizeObject} />
      </Col>
      <Col span={12}>
        <IWL label="T nh" content={student.homePhone} />
      </Col>
      <Col span={12}>
        <IWL label="Nhm i" content={student.prioritizeObject} />
      </Col>
    </Form>
  );
};

export default InfoBackground;



