import React, { Fragment } from 'react';
import IWL from '../component/InformationWithLabel';

const InfoEnrollment = props => {
  const { student } = props;

  return (
    <Fragment>
      <IWL label="Ngoi ng" content={student.foreignLanguage} />
      <IWL label="Khu vc" content={student ? student.area : ''} />
      <IWL label="S bo danh" content={student ? student.code : ''} />
      <IWL label="im tng cng" content={student ? student.totalGrade : ''} />
    </Fragment>
  );
};

export default InfoEnrollment;



