import React from 'react';
import { Col, Form } from 'antd';
// import { SelectAjax } from 'components/common';
// import { getListTown } from 'services/town';
// import { getListNation } from 'services/nation';
// import { getDistricts } from 'services/district';
// import { getComeFrom } from 'services/comeFrom';
import IWL from '../component/InformationWithLabel';

const InfoAddress = props => {
  const { student } = props;

  return (
    <Form className="form-inline-label" layout="vertical">
      <Col span={12}>
        <IWL label="Qu qun" content={student.homeVillage} />
      </Col>
      <Col span={12}>
        <IWL label="Quc tch" content={student.nation} />
      </Col>
      <Col span={12}>
        <IWL label="TP xut thn" content={student.comeFrom} />
      </Col>
      <Col span={12}>
        <IWL label="C thng tr" content={student.permanentAddress} />
      </Col>
      <Col span={12}>
        <IWL label="Tnh/Tp" content={student.town} />
      </Col>
      <Col span={12}>
        <IWL label="Qun/Huyn" content={student.district} />
      </Col>
      <Col span={12}>
        <IWL label="X/Phng" content={student.ward} />
      </Col>
      <Col span={12}>
        <IWL label="a ch bo tin" content={student.emergencyAddress} />
      </Col>
      <Col span={12}>
        <IWL label="Ni  hin nay" content={student.liveAddress} />
      </Col>
    </Form>
  );
};

export default InfoAddress;



